import defaultShortcuts from './default.shortcuts';

export default class Shortcuts {
  constructor() {
    this.default = defaultShortcuts;
    this.shortcuts = {};
    this.setDefault();
  }

  setDefault() {
    this.shortcuts = Object.assign({}, this.default);
  }

  check(keyCode) {
    const { tools } = this.shortcuts;
    let rez = { tool: undefined, type: undefined };
    Object.values(tools).forEach((tool) => {
      if (tool[0] === keyCode) {
        rez = { tool: tool[1], type: tool[2] };
      }
    });
    return rez;
  }
}
